* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

#datetime-picker {
  width: 250px;
  text-align: center;
  margin-bottom: 20px;
  display: inline-block;
}

#data-start {
  width: 50px;
}

.timer {
  width: 300px;
  gap: 10px;
  display: flex;
}

.field {
  text-align: center;
  width: 100%;
  display: block;
}

.value {
  font-size: 18px;
  font-weight: bold;
  display: block;
}

.label {
  display: block;
}

